import { message } from "ant-design-vue";

const copyStatus = new Set<string>();

/**
 * BEM 
 */
export class BEM {
  private readonly block: string;

  constructor(block: string) {
    this.block = block;
  }

  /**
   *  Block : `block`
   */
  b(): string {
    return this.block;
  }

  /**
   *  Element : `block__element`
   * @param element 
   */
  e(element: string): string {
    return `${this.block}__${element}`;
  }

  /**
   *  Modifier : `block--modifier`
   * @param modifier 
   */
  m(modifier: string): string {
    return `${this.block}--${modifier}`;
  }

  /**
   *  Element Modifier : `block__element--modifier`
   * @param element 
   * @param modifier 
   */
  em(element: string, modifier: string): string {
    return `${this.e(element)}--${modifier}`;
  }
}

export function copyText(text: string) {
  return new Promise((resolve) => {
    if (copyStatus.has(text)) return resolve(1);
    copyStatus.add(text);
    setTimeout(() => {
      let result = 0;
      try {
        if (navigator?.clipboard) {
          navigator.clipboard.writeText(text);
          message.success("", 5);
        } else {
          const textarea = document.createElement("textarea");

          const style =
            "position: fixed;z-index: -100;top: -100px;left: -100%;clip: rect(0 0 0 0)";

          textarea.setAttribute("style", style);

          document.body.appendChild(textarea);

          textarea.value = text;
          textarea.select();
          document.execCommand("copy", true);
          document.body.removeChild(textarea);
          message.success("", 5);
        }
      } catch (error) {
        result = -1;
        message.error("", 5);
        console.error("[copyText fail]: ", error);
      }

      setTimeout(() => {
        copyStatus.delete(text);
        resolve(result);
      }, 150);
    });
  });
}
